<template>
  <header :class="{ 'scrolled-nav': scrolledNav }">
    <nav>
      <div class="logo">
        <img src="@/assets/logo.png" alt="logo" />
      </div>
      <ul v-show="!mobile" class="navigation">
        <li class="link"><router-link to="/">Нүүр</router-link></li>
        <li class="link"><router-link to="/">Мэдээ</router-link></li>
        <li class="link"><router-link to="teams">Багууд</router-link></li>
        <li class="link"><router-link to="stats">Статистик</router-link></li>
        <li class="link"><router-link to="standings">Байрлал</router-link></li>
        <li class="link"><router-link to="schedule">Хуваарь</router-link></li>
        <li class="link"><router-link to="login">Нэвтрэх</router-link></li>
      </ul>
      <div class="icon">
        <i
          @click="toggleMobileNav"
          v-show="mobile"
          class="fas fa-bars"
          :class="{ 'icon-active': mobileNav }"
        ></i>
      </div>
      <transition name="mobile-nav">
        <ul v-show="mobileNav" class="dropdown-nav">
          <li class="link">Нүүр</li>
          <li class="link">Мэдээ</li>
          <li class="link">Багууд</li>
          <li class="link">Статистик</li>
          <li class="link">Байрлал</li>
          <li class="link">Хуваарь</li>
          <li class="link">Нэвтрэх</li>
        </ul>
      </transition>
    </nav>
  </header>
</template>

<script>
export default {
  name: "navigation",
  data() {
    return {
      scrolledNav: null,
      mobile: null,
      mobileNav: null,
      windowWidth: null,
    };
  },
  created() {
    window.addEventListener("resize", this.checkScreen);
    this.checkScreen();
  },

  mounted() {
    window.addEventListener("scroll", this.updateScroll);
  },
  methods: {
    toggleMobileNav() {
      this.mobileNav = !this.mobileNav;
    },

    updateScroll() {
      const scrollPos = window.scrollY;
      if (scrollPos > 50) {
        this.scrolledNav = true;
        return;
      }
      this.scrolledNav = false;
    },

    checkScreen() {
      this.windowWidth = window.innerWidth;
      if (this.windowWidth <= 810) {
        this.mobile = true;
        return;
      }
      this.mobile = false;
      this.mobileNav = false;
      return;
    },
  },
};
</script>

